body{
  margin: 0;
}

main {
  background-color: #F3F6F8;
  height: 100vh;
  padding: 10px;
}

.header {
  height: 400px;
  border: 4px solid;
  &__nav {
    display: flex;
    justify-content: space-between;

    .nav__right{
      display: flex;
      width: 150px;
      justify-content: space-between;
    }
  }

  &__search-wrapper {
    height: 200px;
    position: relative;
  }
  // .link__picture {
  //   display: flex;
  //   justify-content: center;
  //   align-items: center;
  //   position: relative;
    
  //   & img {
  //     width: 580px;
  //     position: absolute
  //   }
  // }

  &__search{
    display: flex;
    width: 100%;
    position: absolute;
    bottom: 0;
    justify-content: center;
    
    .search-form {
      display: flex;
      align-items: center;
      background-color: white;
      border: 2px solid green;
      border-radius: 45px;
      width: 55%;

      &__logo{
        margin: 7px;

        img {
          width: 35px;
          height: 35px;
        }
      }

      &__input {
        border: none;
        margin: 5px 10px;
        width: 70%;

        &:focus{
          outline: none;
        }
      }

      &__mic img{
        height: 35px;
      }
    }
  }

  &__icons{
    width: 35%;
    margin: 10px auto;

    .icons__table{
      display: grid;
      grid-template-columns: repeat(4, 0.5fr);
      grid-template-rows: repeat(2, 0.5fr);
      justify-self: center;
      text-align: center;

      & img {
        width: 35px;
        height: 35px;
      }
    }
  }
}

.logo {
  &__main-img{
    justify-content: center;
  }
}